<script>
    import { onMount } from 'svelte';
    import Carousel from '@beyonk/svelte-carousel'
    import { ChevronLeftIcon, ChevronRightIcon } from 'svelte-feather-icons'
    let works = []
    let body = `{allWorks{
  title,
  images{filename, url, blurUpThumb},
  survey{
    datetime
    location{
      coordinates{
        latitude,
        longitude
      },
      description
    }
    
  }
}}`;
    let token = "4453e2d25a25172b02202e78a1e78e";
    const fetchImage = (async () => {
      const response = await fetch(
        'https://graphql.datocms.com/',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': `Bearer ${token}`,
          },
          body: JSON.stringify({
            query: body
          }),
    })
      return await response.json()
	})();
  onMount(async () => {
		const res = await fetchImage;
		works = await res.data.allWorks;
	});

</script>
<style>
  .control :global(svg) {
		width: 100%;
		height: 100%;
		color: #fff;
		border: 2px solid #fff;
		border-radius: 32px;
	}
  .slide-content{
    height: 600px;
    background-size: cover;
    background-position: center;  
    position: relative;
  }
  .title-container{
    position: absolute;
    bottom: 5%;
    right: 5%;
    background-color: white;
  }
</style>

{#await fetchImage then works}
  <Carousel perPage={{ 1200: 3, 800: 2}}>
    <span class="control" slot="left-control">
      <ChevronLeftIcon />
    </span>
    {#each works.data.allWorks as work, i}
      <div class="slide-content item-{i + 1}" style="background-image: url('{work.images[0].url}');">
        <div class="title-container">
          <p class="title">
            {work.title}
          </p>
          <p class="subtitle">
            {work.survey.datetime}
          </p>
        </div>
      </div>
    {/each}
    <span class="control" slot="right-control">
      <ChevronRightIcon />
    </span>
  </Carousel>
{/await}
